html {
    font-size: 10px;
    font-family: sans-serif;
}

p {
    font-size: 3.5rem;
    line-height: 1.5;
}

h1 {
    font-family: 'billabong';
    text-align: center;
    font-size: 12rem;
    font-weight: 100;
    margin: 2rem 0;
    letter-spacing: -1px;
    background: gray;
    background: linear-gradient(to right, red, lime, cyan, blue, purple);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

@font-face {
    font-family: 'billabong';
    src: url("https://cdn.rawgit.com/milktronics/beaglegr.am/master/public/fonts/billabong-webfont.eot");
    src: url("https://cdn.rawgit.com/milktronics/beaglegr.am/master/public/fonts/billabong-webfont.eot?#iefix") format('embedded-opentype'), url("https://cdn.rawgit.com/milktronics/beaglegr.am/master/public/fonts/billabong-webfont.woff") format('woff'), url("https://cdn.rawgit.com/milktronics/beaglegr.am/master/public/fonts/billabong-webfont.ttf") format('truetype'), url("https://cdn.rawgit.com/milktronics/beaglegr.am/master/public/fonts/billabong-webfont.svg#billabongregular") format('svg');
}

.photo-grid{
    max-width: 1000px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    /* flex-direction: column; */
}

.figure {
    flex-basis: calc(33.333% - 4rem);
    border: 4px solid #d3d3d3;
    padding: 2rem;
    flex-grow: 1;
    margin: 0 2rem 2rem 2rem 
}

.post-description {
    text-align: center;
    font-family: 'billabong';
}

.photo {
    width: calc(100% + 4rem);
    margin: -2rem 0rem 0rem -2rem;
}

.button-container{
    display: flex;
    justify-content: center;
}

button {
    flex-basis: 40%;
    color: red;
    font-size: 15px;
    border: 2px solid #f2f2f2;
    line-height: 2;
    padding: 5px;

}

.add-icon {
    margin: 20px auto;
    display: table;
    padding: 40px;
    background: url("https://image.flaticon.com/icons/svg/14/14232.svg") center no-repeat;
    background-size: contain;
    border: none;
}

.form {
    max-width: 360px;
    margin: 0 auto;
    padding: 45px;
    text-align: center;
}

.form input {
    font-family: sans-serif;
    background: #f2f2f2;
    width: 100%;
    border: none;
    padding: 15px;
    box-sizing: border-box; 
    /* box-sizing: content-box; */
    font-size: 14px;
    margin: 0 0 15px;
}

.form button {
    font-family: sans-serif;
    text-transform: uppercase;
    background: #065784;
    width: 100%;
    border: none;
    padding: 15px;
    color: white;
    font-size: 14px;
    cursor: pointer;
}
.form button:hover{
    background: #7B8589;
}

button:hover {
    border-color: blue;
}